</template>

<script>
import * as TWEEN from "@tweenjs/tween.js";
export default {
  name: "IdenMountain",
  mounted() {
    const trees = this.$refs.trees;
    const rear = this.$refs.rear;
    const front = this.$refs.front;

    // Have to manually animate svgs instead of using background-image/position
    // Due to weird issues with blurry animation on mobile
    const position = { x: 0 };
    new TWEEN.Tween(position)
      .to({ x: 3000 }, 120 * 1000)
      .onUpdate(() => {
        const transform = `translate3d(${-position.x}px, 0, 0)`;
        trees.style.transform = transform;
        rear.style.transform = transform;
        front.style.transform = transform;
      })
      .repeat(Infinity)
      .start();
  },
};
</script>

<style scoped>
#animation-instance {
  display: block;
  position: relative;
  z-index: 0;
}

#animation-instance > * {
  display: block;
  height: 19.688rem;
  left: 0;
  position: absolute;
  width: 6000px;
}

.animation-rear {
  will-change: transform;
  bottom: 2rem;
  z-index: 1;
}

.animation-front {
  will-change: transform;
  z-index: 2;
  bottom: 0;
}

.animation-trees {
  will-change: transform;
  bottom: 0;
  z-index: 3;
}

.m {
  fill: #273640;
}

.m_shading,
.h_shading {
  fill: rgba(0, 0, 0, 0.1);
}

.m_trees {
  fill: #102d0c;
}

.t {
  fill: #102d0c;
}

.t_shading {
  fill: #131d1f;
}

.h {
  fill: #374a58;
}

@media (max-width: 768px) {
  #animation-instance {
    height: 15rem;
    margin-bottom: -1.5rem;
  }
  #animation-instance > * {
    height: 15rem;
  }
}
</style>
